.nav2 {
    position: fixed;
    top: 0;
    width: 100%;
    height: 30px;
    z-index: 1000;
    padding: 20px 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition-timing-function: ease-in;
    transition: all 0.5s;
    background-color: transparent;
}

.nav2__black {
    background-color: #111;
}

.nav__left {
    display: flex;
    align-items: center;
}

.nav__logo {
    width: 120px;
    object-fit: contain;
    cursor: pointer;
    margin-right: 20px;
}

.nav__recommend {
    color: #fff;
    font-size: 20px;
    cursor: pointer;
    margin-right: 5px; /* Ï∂îÏ≤úÍ≥º ÌîÑÎ°úÌïÑ Î°úÍ≥† Í∞ÑÍ≤© */
}

.nav__avater {
    width: 40px;
    height: 40px;
    object-fit: cover;
    cursor: pointer;
    margin-left: 2px;
    margin-right: 70px; /* ‚Üê Ïù¥Í±∞ Ï∂îÍ∞ÄÌï¥ÏÑú ÏÇ¥Ïßù ÏôºÏ™ΩÏúºÎ°ú Ïù¥Îèô */
    flex-shrink: 0;
}

.nav__right {
    display: flex;
    align-items: center;
    gap: 10px; /* üí° ÏöîÏÜå Í∞Ñ Ïó¨Î∞±ÏùÄ gapÏúºÎ°ú Ï°∞Ï†à */
    position: relative;
    overflow: visible; /* ÌòπÏãúÎùºÎèÑ Í∞ÄÎ†§ÏßÄÎäî Í±∞ Î∞©ÏßÄ */
    padding-right: 0; /* Ï∂îÍ∞Ä */
}

.nav__login {
    background: none;
    border: none;
    color: #fff;
    font-size: 18px;
    cursor: pointer;
    padding: 10px 20px;
    margin-right: 40px;
}

.nav__login:hover {
    color: #b3b3b3;
}

/* --- Ïó¨Í∏∞Í∞Ä ÏàòÏ†ïÎêú Î∂ÄÎ∂ÑÏûÖÎãàÎã§ --- */
.nav__right .nav__user-id { /* ÏÑ†ÌÉùÏûêÎ•º Îçî Íµ¨Ï≤¥Ï†ÅÏúºÎ°ú Î≥ÄÍ≤ΩÌïòÏó¨ Ïö∞ÏÑ†ÏàúÏúÑÎ•º ÎÜíÏòÄÏäµÎãàÎã§. */
    font-size: 1.1rem; /* Ìè∞Ìä∏ ÌÅ¨Í∏∞ */
