/* src/styles/MemberModelFilters.css */

/* Î∞∞Í≤Ω ÏõÄÏßÅÏûÑ Ïï†ÎãàÎ©îÏù¥ÏÖò ÌÇ§ÌîÑÎ†àÏûÑ (Ïª®ÌÖåÏù¥ÎÑàÏö©) */
@keyframes netflixIntroBackground {
    0% { background-position: 0% 0%; }
    50% { background-position: 100% 100%; }
    100% { background-position: 0% 0%; }
}

/* [Ï∂îÍ∞Ä] Î≤ÑÌäº ÌõÑÍ¥ë Ïï†ÎãàÎ©îÏù¥ÏÖò ÌÇ§ÌîÑÎ†àÏûÑ */
@keyframes buttonHaloPulse {
    from { box-shadow: 0 0 6px rgba(255, 255, 255, 0.5); }
    to { box-shadow: 0 0 14px rgba(255, 255, 255, 0.8); }
}

/* ÌöåÏõê Î™®Îìú ÌïÑÌÑ∞ Ïª®ÌÖåÏù¥ÎÑà (ÎßàÏùåÏóê Îì§Ïñ¥ ÌïòÏã† Î∞∞Í≤Ω Ïú†ÏßÄ) */
.member-mode-filters {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 40px; /* Í∑∏Î£π Í∞Ñ Í∞ÑÍ≤© Ï°∞Ï†ï */
    margin: 20px auto 30px auto;
    padding: 20px 50px;
    border-radius: 8px;
    flex-wrap: wrap;
    position: relative;
    background: radial-gradient(ellipse at center, rgba(190, 30, 30, 0.8) 0%, rgba(108, 23, 25, 0.8) 25%, rgba(20, 20, 20, 1) 75%, #000 100%);
    background-size: 150% 150%;
    background-position: center;
    animation: netflixIntroBackground 10s ease-in-out infinite alternate;
}

/* Í∞úÎ≥Ñ ÌïÑÌÑ∞ Í∑∏Î£π */
.filter-group {
    display: flex;
    flex-direction: column;
    gap: 12px;
    align-items: center;
}

/* [ÏàòÏ†ï] Î†àÏù¥Î∏î(ÌÖçÏä§Ìä∏) Ïä§ÌÉÄÏùº: Í∞ÄÎèÖÏÑ± Í∞úÏÑ† */
.filter-group label {
    font-size: 1rem;
    color: #e0e0e0; /* Îçî Î∞ùÏùÄ ÌöåÏÉâÏúºÎ°ú Î≥ÄÍ≤Ω */
    font-weight: 600;
    text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.7); /* Í∑∏Î¶ºÏûêÎ•º Îã®ÏàúÌôîÌïòÏó¨ ÍπîÎÅîÌïòÍ≤å */
}

/* Î≤ÑÌäºÎì§ÏùÑ Í∞êÏã∏Îäî Í∑∏Î£π */
.button-group {
    display: flex;
    gap: 10px;
}

/* [ÏàòÏ†ï] ÌïÑÌÑ∞ Î≤ÑÌäº Ïä§ÌÉÄÏùº: ÏöîÏ≤≠ÌïòÏã† 'ÌïòÏñÄÏÉâ ÌõÑÍ¥ë' Ï†ÅÏö© */
.filter-button {
    padding: 8px 18px;
    border-radius: 5px;
    background: #333;
    color: #fff;
    font-size: 0.9rem;
    font-weight: 500;
    cursor: pointer;
    transition: all .2s ease-in-out;
    border: 1px solid #555;
    /* ÌïòÏñÄÏÉâ ÌõÑÍ¥ë Ïï†ÎãàÎ©îÏù¥ÏÖò Ï†ÅÏö© */
    box-shadow: 0 0 6px rgba(255, 255, 255, 0.5);
    animation: buttonHaloPulse 2s infinite alternate ease-in-out;
}

/* Î≤ÑÌäº Ìò∏Î≤Ñ Ìö®Í≥º */
.filter-button:hover:not(.selected) {
    background: #4d4d4d;
    border-color: #eee;
    transform: translateY(-2px); /* ÏÇ¥Ïßù Îñ†Ïò§Î•¥Îäî Ìö®Í≥º */
}

/* ÏÑ†ÌÉùÎêú Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.filter-button.selected {
    background: #e50914; /* ÏÑ†ÌÉù Ïãú ÎÑ∑ÌîåÎ¶≠Ïä§ Îπ®Í∞ï */
    border-color: #e50914;
    color: #fff;
    font-weight: 700;
    transform: scale(1.05);
    /* ÏÑ†ÌÉù ÏãúÏóêÎäî ÌõÑÍ¥ë Ïï†ÎãàÎ©îÏù¥ÏÖò Ï§ëÏßÄ */
    animation: none;
    box-shadow: 0 0 15px rgba(229, 9, 20, 0.6); /* ÎåÄÏã† Îπ®Í∞ÑÏÉâ Í∑∏Î¶ºÏûê */
}