/* src/styles/MovieModal.css */

/* Î™®Îã¨ Ïò§Î≤ÑÎ†àÏù¥: Îí∑ Î∞∞Í≤ΩÏùÑ Ïñ¥Îë°Í≤å Ï≤òÎ¶¨, Ïä§ÌÅ¨Î°§ Î∞©ÏßÄ */
.movie-modal-overlay {
    position: fixed; /* Î∑∞Ìè¨Ìä∏ Í∏∞Ï§ÄÏúºÎ°ú Í≥†Ï†ï */
    top: 0;
    left: 0;
    width: 90vw; /* Î∑∞Ìè¨Ìä∏ Ï†ÑÏ≤¥ ÎÑàÎπÑ */
    height: 90vh; /* Î∑∞Ìè¨Ìä∏ Ï†ÑÏ≤¥ ÎÜíÏù¥ */
    background-color: rgba(0, 0, 0, 0.75); /* Î∞òÌà¨Î™Ö Í≤ÄÏùÄÏÉâ Î∞∞Í≤Ω */
    display: flex; /* ÏûêÏãù ÏöîÏÜå(Î™®Îã¨ ÏΩòÌÖêÏ∏†)Î•º Ï†ïÎ†¨ÌïòÍ∏∞ ÏúÑÌï® */
    justify-content: center; /* Í∞ÄÎ°ú Ï§ëÏïô Ï†ïÎ†¨ */
    align-items: flex-start; /* ÏÑ∏Î°ú ÏÉÅÎã® Ï†ïÎ†¨ (JSÏóêÏÑú top Í∞íÏùÑ Ï§Ñ Í≤ÉÏù¥ÎØÄÎ°ú) */
    z-index: 1000; /* Îã§Î•∏ Î™®Îì† ÏΩòÌÖêÏ∏† ÏúÑÏóê Ïò§ÎèÑÎ°ù */
    overflow-y: auto; /* Ïò§Î≤ÑÎ†àÏù¥Ïóê Ïä§ÌÅ¨Î°§Î∞îÍ∞Ä ÏÉùÍ∏∞ÎèÑÎ°ù (Î™®Îã¨ ÏûêÏ≤¥Îäî Ïä§ÌÅ¨Î°§ Ïïà Ìï®) */
    overflow-x: hidden; /* Í∞ÄÎ°ú Ïä§ÌÅ¨Î°§ Î∞©ÏßÄ */
    /* padding-top/bottomÏùÄ ÌïÑÏöî ÏóÜÏùå (align-items: flex-start + JS top Í∞í) */
}

/* Î™®Îã¨ ÏΩòÌÖêÏ∏† Î∞ïÏä§ */
.movie-modal-content {
    background-color: #181818; /* ÎÑ∑ÌîåÎ¶≠Ïä§ Î™®Îã¨ Î∞∞Í≤ΩÏÉâ */
    border-radius: 8px;

    /* === ÌïµÏã¨: Î™®Îã¨ ÌÅ¨Í∏∞ Ï°∞Ï†ï === */
    width: 60%; /* Î™®Îã¨ ÎÑàÎπÑÎ•º 60%Î°ú ÏÑ§Ï†ï (Ïù¥Ï†ÑÎ≥¥Îã§ Îçî Ï§ÑÏûÑ) */
    max-width: 650px; /* ÏµúÎåÄ ÎÑàÎπÑÎ•º 700pxÏóêÏÑú 650pxÎ°ú Ï§ÑÏûÑ */
    max-height: 90vh; /* Î∑∞Ìè¨Ìä∏ ÎÜíÏù¥Ïùò 90%Î•º ÎÑòÏßÄ ÏïäÎèÑÎ°ù Ï†úÌïú */

    display: flex;
    flex-direction: column;

    /* === ÌïµÏã¨: JSÏóêÏÑú topÏùÑ Ï†úÏñ¥ÌïòÍ∏∞ ÏúÑÌïú position: absolute === */
    position: absolute; /* overlayÎ•º Í∏∞Ï§ÄÏúºÎ°ú ÏúÑÏπò Ï°∞Ï†ï (JSÏóêÏÑú top Í∞íÏùÑ Ï§Ñ Í≤É) */
    left: 50%; /* Í∞ÄÎ°ú Ï§ëÏïô Ï†ïÎ†¨ÏùÄ CSSÏóêÏÑú Îã¥Îãπ */
    /* topÍ≥º transform (translateY)ÏùÄ JSÏóêÏÑú ÎèôÏ†ÅÏúºÎ°ú ÏÑ§Ï†ïÎê©ÎãàÎã§. */
    /* transform: translateX(-50%); Îäî Ï¥àÍ∏∞Í∞íÏù¥Î©∞, JSÏóêÏÑú ÎçÆÏñ¥Ïì∏ Ïàò ÏûàÏäµÎãàÎã§. */

    color: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    animation: modalAppear 0.3s forwards; /* Îì±Ïû• Ïï†ÎãàÎ©îÏù¥ÏÖò */
    overflow-y: auto; /* Î™®Îã¨ ÏΩòÌÖêÏ∏†Í∞Ä Í∏∏Ïñ¥ÏßÄÎ©¥ Î™®Îã¨ ÎÇ¥Î∂ÄÏóêÏÑúÎßå Ïä§ÌÅ¨Î°§ Î∞úÏÉù */
    overflow-x: hidden; /* Í∞ÄÎ°ú Ïä§ÌÅ¨Î°§ Î∞©ÏßÄ */
}

/* Î™®Îã¨ Îì±Ïû• Ïï†ÎãàÎ©îÏù¥ÏÖò (transformÏùÑ JSÏóêÏÑú Ï†úÏñ¥ÌïòÎØÄÎ°ú, opacityÏôÄ scaleÎßå) */
@keyframes modalAppear {
    from { opacity: 0; transform: scale(0.95) translateX(-50%); } /* JSÏùò Ï¥àÍ∏∞ transformÍ≥º ÏùºÏπò */
    to { opacity: 1; transform: scale(1) translateX(-50%); } /* JSÏùò Ï¥àÍ∏∞ transformÍ≥º ÏùºÏπò */
}


/* Îã´Í∏∞ Î≤ÑÌäº */
.movie-modal-close-button {
    position: absolute;
    top: 15px;
    right: 15px;
    background-color: rgba(255, 255, 255, 0.1);
    border: none;
    border-radius: 50%;
    color: white;
    font-size: 20px;
    width: 36px;
    height: 36px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    z-index: 10;
    transition: background-color 0.3s ease;
}

.movie-modal-close-button:hover {
    background-color: rgba(255, 255, 255, 0.3);
}

/* ÎØ∏ÎîîÏñ¥ ÏòÅÏó≠ (Ìä∏Î†àÏùºÎü¨/Ìè¨Ïä§ÌÑ∞ + Ïò§Î≤ÑÎ†àÏù¥ Ï†ïÎ≥¥) */
.movie-modal-media-area {
    position: relative;
    width: 100%;
    padding-top: 56.25%; /* 16:9 ÎπÑÏú® Ïú†ÏßÄ */
    background-color: black;
    overflow: hidden;
}

.movie-modal-trailer,
.movie-modal-poster-large {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

/* ÎØ∏ÎîîÏñ¥ ÏúÑÏóê Í≤πÏ≥êÏßÄÎäî Ï†ïÎ≥¥ Î∞è Î≤ÑÌäº ÏòÅÏó≠ */
.movie-modal-overlay-buttons {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 20px 40px;
    background: linear-gradient(to top, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0) 100%);
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    color: white;
}

.movie-modal-title {
    font-size: 2.2em;
    margin-bottom: 15px;
    text-shadow: 2px 2px 4px rgba(0,0,0,0.7);
}

.modal-buttons-group {
    display: flex;
    gap: 15px;
}

.modal-play-button,
.modal-info-button {
    background-color: white;
    color: black;
    border: none;
    padding: 10px 25px;
    border-radius: 5px;
    font-size: 1.1em;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: center;
}

.modal-info-button {
    background-color: rgba(109, 109, 110, 0.7);
    color: white;
}

.modal-play-button:hover {
    background-color: rgba(255, 255, 255, 0.8);
}

.modal-info-button:hover {
    background-color: rgba(109, 109, 110, 0.9);
}

.modal-play-button i,
.modal-info-button i {
    margin-right: 8px;
    font-size: 1.2em;
}

/* ÏÉÅÏÑ∏ Ï†ïÎ≥¥ ÏòÅÏó≠ */
.movie-modal-details-area {
    padding: 30px 40px;
    display: flex;
    flex-direction: column;
    overflow-y: auto; /* Ïù¥ ÏòÅÏó≠ ÏûêÏ≤¥ÏóêÏÑú Ïä§ÌÅ¨Î°§ Î∞úÏÉù */
    flex-grow: 1;
}

.movie-modal-meta {
    display: flex;
    align-items: center;
    gap: 15px;
    font-size: 1em;
    margin-bottom: 20px;
    color: #a3a3a3;
}

.match-percentage {
    color: #46d369;
    font-weight: bold;
}

.release-year { /* Í∏∞Î≥∏ Ïä§ÌÉÄÏùº */ }
.age-rating {
    border: 1px solid #a3a3a3;
    padding: 2px 6px;
    border-radius: 3px;
    font-size: 0.9em;
}

.hd-badge {
    border: 1px solid #a3a3a3;
    padding: 2px 6px;
    border-radius: 3px;
    font-size: 0.9em;
}

.movie-modal-overview-section {
    display: flex;
    justify-content: space-between;
    gap: 40px;
}

.movie-modal-overview {
    flex: 2;
    font-size: 1.1em;
    line-height: 1.6;
    color: #e5e5e5;
}

.movie-modal-genre-rating {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 15px;
    font-size: 1em;
}

.modal-genres {
    color: #a3a3a3;
}
.modal-genres strong {
    color: #e5e5e5;
}

.modal-rating-stars {
    display: flex;
    align-items: center;
    gap: 5px;
}

/* react-rating Ïú†ÎãàÏΩîÎìú Î≥ÑÌëú Ïä§ÌÉÄÏùº */
.react-rating-modal-stars .icon-star {
    font-size: 1.2em;
    line-height: 1;
    color: #ccc;
}

.react-rating-modal-stars .icon-star:nth-child(-n + 5) {
    color: #ffd700;
}

.modal-rating-score {
    margin-left: 5px;
    font-size: 1em;
    color: #a3a3a3;
}