/* src/styles/Header.css */

/* Google Fonts ÏûÑÌè¨Ìä∏ (Header Ï†ÑÏö© Ìè∞Ìä∏) */
/* Noto Sans KR Ìè∞Ìä∏ ÏûÑÌè¨Ìä∏ (Í∏∞Î≥∏ body Ìè∞Ìä∏Î°ú Í∞ÄÏ†ï) */
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500;700&display=swap');

.header {
    position: fixed; /* ÏÉÅÎã® Í≥†Ï†ï */
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 50px; /* ÏÉÅÌïò Ìå®Îî©ÏùÑ ÎäòÎ†§ Ìó§Îçî ÏÑ∏Î°ú ÌÅ¨Í∏∞ Ï°∞Ï†à */
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0) 100%); /* Ï¥àÍ∏∞ Ìà¨Î™Ö Í∑∏ÎùºÎç∞Ïù¥ÏÖò */
    z-index: 1000; /* Îã§Î•∏ ÏΩòÌÖêÏ∏† ÏúÑÏóê Ïò§ÎèÑÎ°ù */
    transition: background-color 0.5s ease-in-out; /* Ïä§ÌÅ¨Î°§ Ïãú Î∞∞Í≤Ω Ï†ÑÌôò Ìö®Í≥º */
}

.header-scrolled {
    background-color: rgba(20, 20, 20, 1); /* Ïä§ÌÅ¨Î°§ ÎÇ¥Î†∏ÏùÑ Îïå Í≤ÄÏùÄÏÉâ Î∞∞Í≤Ω */
}

.header-left,
.header-right {
    display: flex;
    align-items: center;
}

.netflix-logo img {
    height: 30px; /* Î°úÍ≥† ÌÅ¨Í∏∞ Ï°∞Ï†à */
    margin-right: 20px;
}

.main-nav ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
}

.main-nav li {
