    font-family: 'Noto Sans KR', sans-serif; /* Ìè∞Ìä∏ Ï†ÅÏö© */
}

.main-nav a:hover {
    color: #fff;
}

.header-right {
    gap: 20px; /* ÏïÑÏù¥ÏΩòÎì§ ÏÇ¨Ïù¥ Í∞ÑÍ≤© */
}

.search-container {
    position: relative;
    display: flex;
    align-items: center;
}

.search-icon-button {
    background: none;
    border: none;
    color: #fff;
    font-size: 18px;
    cursor: pointer;
    padding: 5px;
}

.search-form {
    display: flex;
    background-color: rgba(0, 0, 0, 0.8);
    border: 1px solid rgba(255, 255, 255, 0.7);
    border-radius: 4px;
    margin-left: 10px;
}

.search-input {
    background: transparent;
    border: none;
    color: #fff;
    padding: 5px 10px;
    outline: none;
    font-size: 14px;
    font-family: 'Noto Sans KR', sans-serif; /* Ìè∞Ìä∏ Ï†ÅÏö© */
}

.header-icon-button {
    background: none;
    border: none;
    color: #fff;
    font-size: 18px;
    cursor: pointer;
    padding: 5px;
}

.header-icon-button i {
