}

/* ÌöåÏõê Î™®Îìú ÌÜ†Í∏Ä Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.member-mode-toggle {
    display: flex;
    align-items: center;
    gap: 5px;
    font-size: 16px;
    font-weight: 500;
    color: #e5e5e5; /* Í∏∞Î≥∏ ÏÉâÏÉÅ */
    border: 1px solid #555; /* ÎÑ∑ÌîåÎ¶≠Ïä§ Î≤ÑÌäºÏ≤òÎüº ÏïΩÍ∞ÑÏùò ÌÖåÎëêÎ¶¨ */
    padding: 8px 12px; /* Ìå®Îî© */
    border-radius: 4px; /* Îë•Í∑º Î™®ÏÑúÎ¶¨ */
    transition: all 0.3s ease; /* Ìò∏Î≤Ñ Î∞è ÌôúÏÑ±Ìôî Ïãú Î∂ÄÎìúÎü¨Ïö¥ Ï†ÑÌôò */
    background-color: transparent; /* Î∞∞Í≤Ω Ìà¨Î™Ö */
    position: relative; /* Îπõ Ìö®Í≥ºÎ•º ÏúÑÌï¥ */
    overflow: hidden; /* Îπõ Ìö®Í≥ºÍ∞Ä Î∞ñÏúºÎ°ú ÎÇòÍ∞ÄÏßÄ ÏïäÎèÑÎ°ù */
    font-family: 'Noto Sans KR', sans-serif; /* Ìè∞Ìä∏ Ï†ÅÏö© */
}

.member-mode-toggle:hover {
    color: #fff;
    border-color: #e50914; /* Ìò∏Î≤Ñ Ïãú Îπ®Í∞ÑÏÉâ ÌÖåÎëêÎ¶¨ */
    box-shadow: 0 0 8px rgba(229, 9, 20, 0.5); /* Í∑∏Î¶ºÏûê Ìö®Í≥º */
}

.member-mode-toggle.active {
    color: #fff; /* ÌôúÏÑ±Ìôî ÏÉÅÌÉúÏùº Îïå Í∏ÄÏûêÏÉâ */
    background-color: #e50914; /* ÌôúÏÑ±Ìôî Ïãú Î∞∞Í≤ΩÏÉâ */
    border-color: #e50914;
    box-shadow: 0 0 15px rgba(229, 9, 20, 0.7);
}

/* Î¨¥ÏßÄÍ∞ú Îπõ Ïï†ÎãàÎ©îÏù¥ÏÖò (ÌöåÏõê Î™®Îìú Î≤ÑÌäº) */
.member-mode-toggle.glowing-rainbow {
    background: linear-gradient(90deg, #ff0000, #ff7f00, #ffff00, #00ff00, #0000ff, #4b0082, #9400d3);
    background-size: 400% 100%; /* Ïï†ÎãàÎ©îÏù¥ÏÖòÏùÑ ÏúÑÌïú ÌÅ¨Í∏∞ ÌôïÏû• */
    animation: rainbowGlow 4s linear infinite, pulsateBorder 1.5s ease-out infinite alternate;
    border: none; /* Í∑∏ÎùºÎç∞Ïù¥ÏÖòÏù¥ Ï†ÅÏö©ÎêòÎØÄÎ°ú ÌÖåÎëêÎ¶¨ Ï†úÍ±∞ */
}

.member-mode-toggle.glowing-rainbow .member-mode-text,
.member-mode-toggle.glowing-rainbow i {
    color: #fff; /* Í∏ÄÏûêÏÉâÏù¥ Í∑∏ÎùºÎç∞Ïù¥ÏÖòÏóê Î¨ªÌûàÏßÄ ÏïäÎèÑÎ°ù Ìù∞ÏÉâÏúºÎ°ú Ïú†ÏßÄ */
    text-shadow: 0 0 5px rgba(0,0,0,0.5); /* Í∏ÄÏûêÏóê Í∑∏Î¶ºÏûê Ï§òÏÑú Í∞ÄÎèÖÏÑ± ÎÜíÏûÑ */
    z-index: 2; /* ÎπõÎ≥¥Îã§ ÏúÑÏóê Ïò§ÎèÑÎ°ù */
    position: relative; /* z-indexÎ•º ÏúÑÌï¥ */
}

.member-mode-toggle.glowing-rainbow::before {
    content: '';
    position: absolute;
    top: -2px;
    left: -2px;
    right: -2px;
    bottom: -2px;
    background: inherit;
    border-radius: 6px;
    filter: blur(8px);
    opacity: 0.7;
    z-index: 1;
}

/* Ïï†ÎãàÎ©îÏù¥ÏÖò ÌÇ§ÌîÑÎ†àÏûÑ (HeaderÏôÄ MainContent Î™®ÎëêÏóêÏÑú ÏÇ¨Ïö©Îê† Ïàò ÏûàÏùå) */
@keyframes rainbowGlow {
    0% { background-position: 0% 50%; }
    100% { background-position: 100% 50%; }
}

@keyframes pulsateBorder {
    from { transform: scale(1); box-shadow: 0 0 10px rgba(255, 255, 255, 0.5); }
    to { transform: scale(1.03); box-shadow: 0 0 20px rgba(255, 255, 255, 0.8); }
}