/* src/styles/recommendation.css */

/* --- recommendation-overlay Î™®Îã¨ Ïä§ÌÉÄÏùº --- */
.recommendation-overlay {
    position: fixed;
    top: 150px; /* Ìó§Îçî ÏïÑÎûòÏóêÏÑú ÏãúÏûë */
    left: 0;
    width: 400px; /* Í∞ÄÎ°ú ÎÑàÎπÑ Ïú†ÏßÄ (ÌïÑÏöîÏãú Ï°∞Ï†à) */

    /* ÏÑ∏Î°ú Í∏∏Ïù¥ Ï°∞Ï†à: bottom: 0; Ï†úÍ±∞ÌïòÍ≥† height auto ÎòêÎäî Ï†ÅÏ†ï ÎÜíÏù¥ ÏÑ§Ï†ï */
    height: auto; /* ÏΩòÌÖêÏ∏† ÎÜíÏù¥Ïóê ÎßûÍ≤å ÏûêÎèô Ï°∞Ï†à */
    /* ÎßåÏïΩ ÌäπÏ†ï ÏµúÏÜå ÎÜíÏù¥ÎÇò ÏµúÎåÄ ÎÜíÏù¥Î•º ÏõêÌïúÎã§Î©¥ ÏïÑÎûò Ï£ºÏÑùÏùÑ Ìï¥Ï†úÌïòÍ≥† Ï°∞Ï†àÌïòÏÑ∏Ïöî. */
    /* min-height: 400px; */ /* ÏµúÏÜå ÎÜíÏù¥ */
    max-height: calc(100vh - 150px - 50px); /* ÌôîÎ©¥ ÎÜíÏù¥ - top - ÏïÑÎûò Ïó¨Î∞± (Ïä§ÌÅ¨Î°§ Í∞ÄÎä•) */
    /* 50pxÏùÄ ÏòàÏãú Î∞îÎã• Ïó¨Î∞±. Ï°∞Ï†à Í∞ÄÎä• */
    /* bottom: 0; (Ï†úÍ±∞) */ /* ÏÑ∏Î°úÎ°ú ÌôîÎ©¥ ÎÅùÍπåÏßÄ ÌôïÏû•ÎêòÎäî Í≤ÉÏùÑ Î∞©ÏßÄ */

    background: rgba(0, 0, 0, 0.85); /* Í≤ÄÏùÄÏÉâ Î∞òÌà¨Î™Ö Î∞∞Í≤Ω Ïú†ÏßÄ */

    z-index: 1000;
    display: flex;
    flex-direction: column;
    overflow-y: auto; /* ÎÇ¥Ïö©Ïù¥ ÎÑòÏπ† Îïå Ïä§ÌÅ¨Î°§Î∞î ÏÉùÏÑ± */

    /* Î™®Îã¨ ÎÇ¥Î∂Ä ÏΩòÌÖêÏ∏†Ïùò Ìå®Îî© */
    padding-top: 25px; /* ÏÉÅÎã® Ìå®Îî© */
    padding-bottom: 25px; /* ÌïòÎã® Ìå®Îî© */
    padding-left: 20px;
    padding-right: 20px;

    /* ÏΩòÌÖêÏ∏†Î•º ÏôºÏ™Ω Ï†ïÎ†¨ */
    align-items: flex-start;
    justify-content: flex-start;

    /* Ï¥àÍ∏∞ ÏÉÅÌÉú Î∞è Ïï†ÎãàÎ©îÏù¥ÏÖò */
    opacity: 0;
    transform: translateX(-100%);
    animation: fadeInSlideIn 0.6s ease-out forwards;
}

/* ÎÇòÌÉÄÎÇ† Îïå (ÌéòÏù¥Îìú Ïù∏ + Ïä¨ÎùºÏù¥Îìú Ïù∏) Ïï†ÎãàÎ©îÏù¥ÏÖò */
@keyframes fadeInSlideIn {
    from {
        opacity: 0;
        transform: translateX(-100%);
    }
    to {
        opacity: 1;
        transform: translateX(0);
    }
}

/* ÏÇ¨ÎùºÏßà Îïå (ÌéòÏù¥Îìú ÏïÑÏõÉ + Ïä¨ÎùºÏù¥Îìú ÏïÑÏõÉ) Ïï†ÎãàÎ©îÏù¥ÏÖò */
@keyframes fadeOutSlideOut {
    from {
        opacity: 1;
        transform: translateX(0);
    }
    to {
        opacity: 0;
        transform: translateX(-100%);
    }
}

.recommendation-overlay.closing {
    animation: fadeOutSlideOut 0.4s ease-out forwards;
    pointer-events: none;
}


/* --- Ïã§Ï†ú Î™®Îã¨ ÏΩòÌÖêÏ∏†Î•º Í∞êÏã∏Îäî ÎÇ¥Î∂Ä wrapper Ïä§ÌÉÄÏùº --- */
.modal-content-wrapper {
    background-color: #1a1a1a;
    color: white;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.7);
    width: 100%; /* Î∂ÄÎ™® (recommendation-overlay)Ïùò Ìå®Îî©ÏùÑ Ï†úÏô∏Ìïú Ï†ÑÏ≤¥ ÎÑàÎπÑ ÏÇ¨Ïö© */
    box-sizing: border-box;
}


.recommendation-title {
    font-size: 1.6rem;
    font-weight: 700;
    color: #fff;
    margin-bottom: 20px;
    text-transform: uppercase;
    text-align: center;
}

.recommendation-form {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    align-items: flex-start;
}

.form-field {
    display: flex;
    flex-direction: column;
    min-width: 120px;
    flex: 1;
    margin-bottom: 10px;
}

.form-field label {
    font-size: 0.95rem;
    color: #e0e0e0;
    margin-bottom: 6px;
    font-weight: 500;
}

.form-field select,
.form-field input {
    padding: 8px;
    font-size: 0.9rem;
    border: none;
    border-radius: 4px;
    background: #222;
    color: #fff;
    outline: none;
    transition: background 0.3s;
}

.form-field select:hover,
.form-field input:hover {
    background: #333;
}

.form-field select:disabled {
    background: #444;
    cursor: not-allowed;
}

.genre-list {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
}

.genre-item {
    background: #333;
    padding: 8px 15px;
    border-radius: 20px;
    font-size: 0.85rem;
    color: #fff;
    cursor: pointer;
    transition: background 0.3s, transform 0.2s;
}

.genre-item.selected {
    background: #e50914;
    transform: scale(1.05);
}

.genre-item:hover {
    background: #555;
}

.range-container {
    position: relative;
    width: 100%;
    height: 20px;
}

.range-track {
    position: absolute;
    height: 6px;
    background: #333;
    border-radius: 3px;
    top: 7px;
    width: 100%;
    z-index: 1;
}

.range-selected {
    position: absolute;
    height: 6px;
    background: #e50914;
    border-radius: 3px;
    top: 7px;
    z-index: 2;
}

input[type="range"] {
    -webkit-appearance: none;
    width: 100%;
    height: 6px;
    background: transparent;
    outline: none;
    position: absolute;
    top: 7px;
    z-index: 3;
    pointer-events: none;
}

input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 16px;
    height: 16px;
    background: #e50914;
    border-radius: 50%;
    cursor: pointer;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
    pointer-events: auto;
}

input[type="range"]::-webkit-slider-thumb:hover {
    background: #f40612;
    transform: scale(1.1);
}

input[type="range"]::-moz-range-thumb {
    width: 16px;
    height: 16px;
    background: #e50914;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
    pointer-events: auto;
}

input[type="range"]::-moz-range-thumb:hover {
    background: #f40612;
    transform: scale(1.1);
}

.form-buttons {
    display: flex;
    gap: 10px;
    justify-content: center;
    margin-top: 20px;
}

.form-button {
    padding: 10px 20px;
    font-size: 0.9rem;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background 0.3s;
    font-weight: 500;
}

.form-button-primary {
    background: #e50914;
    color: #fff;
}

.form-button-primary:hover {
    background: #f40612;
}

.form-button-primary:disabled {
    background: #555;
    cursor: not-allowed;
}

.form-button-secondary {
    background: #555;
    color: #fff;
}

.form-button-secondary:hover {
    background: #666;
}