.complex-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
    gap: 15px;
    padding: 20px;
}

.complex-grid-item {
    background: rgba(0, 0, 0, 0.8); /* Îß§Ïö∞ Ìù¨ÎØ∏Ìïú Í≤ÄÏùÄÏÉâ (10% Î∂àÌà¨Î™ÖÎèÑ) */
    border-radius: 4px;
    overflow: hidden;
    text-align: center;
    transition: transform 0.2s;
}

.complex-grid-poster {
    width: 100%;
    height: auto;
}

.complex-grid-info {
    padding: 5px;
    color: #fff;
    font-size: 12px;
}

.complex-grid-rating {
    display: flex;
    align-items: center;
    gap: 2px; /* Î≥Ñ ÏïÑÏù¥ÏΩòÍ≥º ÌèâÏ†ê Ïà´Ïûê ÏÇ¨Ïù¥Ïùò Í∞ÑÍ≤© */
    margin-top: 5px;
}


