    font-size: 14px; /* ÏõêÌïòÎäî Î≥Ñ ÌÅ¨Í∏∞ */
    line-height: 1; /* Ìè∞Ìä∏ Ï†ïÎ†¨ Î¨∏Ï†ú Ìï¥Í≤∞ (ÌïÑÏöîÏãú) */
}


.react-rating-stars .icon-star {
